import React from 'react';
import { NavLink } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux';
import { RootStore } from '../../utils/TypeScript';
import { deleteCategory } from '../../redux/actions/categoryAction';
import { deleteBlog } from '../../redux/actions/blogAction';

const Blog = () => {
	const { category, blog, auth } = useSelector((state: RootStore) => state);

	const dispatch = useDispatch();

	const handleDelete = (id: string) => {
		if (!auth.accessToken) return;
		if (window.confirm('Bn c mun xo bi vit ny khng ?')) {
			dispatch(deleteBlog(id, auth.accessToken));
		}
	};
	return (
		<div className="category-main">
			<div className="category-header">
				<h2 className="category-title">Danh sch bi vit</h2>
				<NavLink to="/admin/createBlog" className="category-new btn">
					Thm mi
				</NavLink>
			</div>
			<div className="category-body">
				<table className="table">
					<thead>
						<tr>
							<th className="col">Tn bi vit</th>
							<th className="col">Slug</th>
							<th className="col">Danh mc</th>
							<th className="col">Ngy ng</th>
							<th className="col"></th>
						</tr>
					</thead>
					<tbody>
						{blog.map((el, index) => {
							return (
								<tr className="item-list" key={index}>
									<td className="name" data-label="Tn danh mc">
										{el.title}
									</td>
									<td className="slug" data-label="Slug">
										{el.slug}
									</td>
									<td className="category-blog" data-label="Danh mc">
										{
											category.find((element) => element._id === el.category)
												?.name
										}
									</td>
									<td className="date" data-label="Ngy ng">
										{new Date(el.createdAt).toLocaleDateString('vi')}
									</td>
									<td className="nav-btn">
										<NavLink
											to={`/admin/blog/${el._id}`}
											className="update-btn"
										>
											<i className="bx bxs-edit-alt"></i>Sa
										</NavLink>
										<div
											className="delete-btn"
											onClick={() => handleDelete(el._id)}
										>
											<i className="bx bx-trash"></i>
											Xo
										</div>
									</td>
								</tr>
							);
						})}
					</tbody>
				</table>
			</div>
		</div>
	);
};

export default Blog;
